.outerInventoryDisplay {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.inventoryDisplayTitle {
    width: 100%;
    text-align: center;
    font-size: 1em;
    font-weight: bold;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

.inventoryDisplayCarryDragLiftPushTable {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
    text-align: center;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    border-top: 4px solid black;
}

.inventoryDisplayCarryDragLiftPushTable > * {
    padding-left: 0.5em;
    padding-right: 0.5em;
    padding-top: 0.25em;
    padding-bottom: 0.25em;
}

.inventoryDisplayItems {
    display: grid;
    width: 100%;
    grid-template-columns: auto 1fr auto auto auto;
    text-align: center;
}

.inventoryDisplayItems > .firstCol {
    border-left: 0;
    padding-left: 1em;
}

.inventoryDisplayItems > * {
    font-size: 0.8em;
    padding: 0.5em;
    text-align: center;
    border-top: 4px solid black;
    align-content: center;
}

.inventoryDisplayItems > .lastCol {
    border-right: 0;
    padding-right: 1em;
}

.inventoryDisplayItem {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.25em;
    min-height: 3.5em;
}

.inventoryDisplayItem.lastCol {
    font-size: 1em;
}

.inventoryDisplayItem.lastCol:hover {
    text-decoration: underline;
}

.inventoryDisplayButtons {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    border-top: 4px solid black;
}

.inventoryDisplayButtons > * {
    padding: 1em;
}
