.outerDiv {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.outerDiv > * {
    padding-bottom: 1em;
}

.outerDiv > *:last-child {
    padding-bottom: 0;
}

.playerName {
    white-space: pre-line;
    font-weight: bold;
}

.healthBarAndDefense {
    display: flex;
    flex-direction: row;
    gap: 1em;
}

.healthBarAndDefense > *:first-child {
    flex-grow: 1;
}

.encounterStats {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1em;
}

.baseStats {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    row-gap: 1em;
    column-gap: 1em;
    max-height: 70em;
}

.baseStats > * {
    width: calc(50% - 0.5em);
}

.textEntry {
    width: 0;
    min-width: 100%;
}

.textWithLinesWrapper {
    white-space: pre-line;
}

.featuresListFeatureMainHeading {
    font-weight: bold;
    text-decoration: underline;
    font-size: 1.5em;
}

.featuresListFeatureHeading {
    font-weight: bold;
    font-size: 1.20em;
}